.devices-list-main {
  display: flex;
  width: 100%;
  flex-direction: column;
  .devices-list-table-header-ctr {
    box-sizing: border-box;
    display: flex;
    width: 100%;
    padding: 0rem 2rem;
    flex-direction: column;
    align-items: flex-start;
    align-self: stretch;
    .devices-list-table-header {
      display: flex;
      align-items: flex-start;
      align-self: stretch;
      border-bottom: 1px solid var(--neutral-neutral-03-light, #ededf0);
      > * {
        display: flex;
        padding: 0.375rem 0.5rem;
        align-items: center;
        gap: 0.5rem;
      }
      .devices-list-table-header-icon {
        display: flex;
        padding: 0.375rem 0.5rem;
        align-items: center;
        gap: 0.5rem;
        width: 2.25rem;
        height: 2rem;
        justify-content: center;
      }
      .devices-list-table-header-col {
        // Need to deviate from Figma slightly
        display: flex;
        align-items: center;
        gap: 0.5rem;
        flex: 1 0 0;
        cursor: default;
        .devices-list-table-header-text-ctr {
          padding: 0.375rem 0.5rem;
          // Add borders invisibly to avoid jank on hover, focus and active
          border-top: 1px solid transparent;
          border-bottom: 1px solid transparent;
          &:hover {
            border-bottom-color: var(--neutral-neutral-03-light, #ededf0);
            background: var(--neutral-web-unifi-color-neutral-02, #f4f5f6);
          }
          &:focus,
          &:active {
            border-top-color: var(--primary-web-unifi-color-ublue-06, #006fff);
            border-bottom: 1px solid var(--primary-web-unifi-color-ublue-06, #006fff);
          }
        }
      }
    }
  }

  .devices-list-table-ctr {
    box-sizing: border-box;
    display: flex;
    width: 100%;
    padding: 0rem 2rem;
    flex-direction: column;
    align-items: flex-start;
    overflow-y: auto;
    // Remove scrollbar
    // Chrome and Safari use nonstandard ::-webkit-scroller
    // A standard scrollbar-width is coming, but only fully supported by Firefox right now.
    &::-webkit-scrollbar {
      display: none;
    }
    scrollbar-width: none;
    .devices-list-table {
      display: flex;
      align-items: flex-start;
      align-self: stretch;
      .devices-list-table-images {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        .devices-list-table-images-ctr {
          display: flex;
          border: none;
          background: transparent;
          cursor: pointer;
          width: 2.25rem;
          padding: 0.375rem 0.5rem;
          justify-content: center;
          align-items: center;
          gap: 0.5rem;
          border-bottom: 1px solid var(--neutral-neutral-03-light, #ededf0);
          .devices-list-table-images-thumbnail {
            width: 1.25rem;
            height: 1.25rem;
            flex-shrink: 0;
            .devices-list-table-images-base {
              display: flex;
              width: 1.25rem;
              height: 1.25rem;
              flex-shrink: 0;
              // background: rgba(196, 196, 196, 0);
              .devices-list-table-images-image {
                width: 1.35419rem;
                height: 1.35419rem;
                flex-shrink: 0;
              }
            }
          }
        }
      }
      .devices-list-table-product-lines {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        flex: 1 0 0;
      }
      .devices-list-table-product-names {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        flex: 1 0 0;
      }
    }
    .devices-list-table-product-lines {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      flex: 1 0 0;
      .devices-list-table-product-line {
        display: flex;
        border: none;
        background: transparent;
        cursor: pointer;
        padding: 0.375rem 0.5rem;
        align-items: center;
        gap: 0.5rem;
        align-self: stretch;
        border-bottom: 1px solid var(--neutral-neutral-03-light, #ededf0);
        .devices-list-table-product-line-text {
          color: var(--text-text-2-light, rgba(0, 0, 0, 0.65));
        }
      }
    }
    .devices-list-table-product-names {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      flex: 1 0 0;
      .devices-list-table-product-name {
        display: flex;
        border: none;
        background: transparent;
        cursor: pointer;
        padding: 0.375rem 0.5rem;
        align-items: center;
        gap: 0.5rem;
        align-self: stretch;
        border-bottom: 1px solid var(--neutral-neutral-03-light, #ededf0);
        .devices-list-table-product-name-text {
          color: var(--text-text-3-light, rgba(0, 0, 0, 0.45));
        }
      }
    }
  }
}
